import * as rules from 'vee-validate/dist/rules'
import {
  extend,
} from 'vee-validate'
import { dict } from './dict'

export function trans(i18n) {
  function _() {
    return i18n.t.apply(i18n, arguments)
  }
  // loop over all rules
  for (const rule in rules) {
    // add the rule
    extend(rule, { ...rules[rule], message: dict(_)[rule] })
  }
}



