import VeeI18n, { trans, compileMessages } from '@aspectus/vee-i18n/src/main'
import Vue from 'vue'

const dateTimeFormats = {}

Vue.use(VeeI18n)
export function createI18n({ Vue, I18n }) {

  const i18n = new I18n({
    ...compileMessages,
    silentTranslationWarn: true,
    dateTimeFormats,
  })
  trans(i18n)

  return i18n
}



